import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import Home from './components/Home';
import Game from './components/Game/Game';

const categories = [
  "El ser humano",
  "Perdido en el mundo digital",
  "Hogar, dulce hogar",
  "Muero de hambre y sed"
];

const App = () => {
  return (
    <Router>
      <Switch>
        <Route exact path="/"
          component={() => <Home /> }
        />
        <Route exact path="/game/:id(\d+)"
          render={({match}) => (
            <Game match={ match } categories={ categories } /> 
          )}
        />
        <Route default component={ Home } />
      </Switch>
    </Router>
  );
}

export default App;